
.sfc-wrapper .sfc-month-hint {
  text-align: center;
}
.sfc-wrapper .sfc-table {
  width: 800px;
  margin: auto;
  table-layout: fixed;
  border-collapse: collapse;
  border-spacing: 0;
}
.sfc-wrapper .sfc-table-head {
  text-align: center;
  background-color: #35495e;
  color: #fff;
}
.sfc-wrapper .sfc-table-head td {
  border: 1px solid #666;
}
.sfc-wrapper .sfc-table-body {
  border: 1px solid #666;
}
.sfc-wrapper .sfc-date {
  text-align: right;
  font-size: 14px;
}
.sfc-wrapper .sfc-date .not-current-month {
  color: #aaa;
}
.sfc-wrapper .sfc-date td {
  border-left: 1px solid #666;
  border-right: 1px solid #666;
}
.sfc-wrapper .sfc-events .sfc-event-item {
  border: 1px solid #666;
  background-color: #42b983;
  overflow: hidden;
  text-overflow: ellipsis;
}
.sfc-wrapper .sfc-events td {
  border-left: 1px solid #666;
  border-right: 1px solid #666;
}


